body {
  margin: 0;
  font-family: 'Press Start 2P', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./components/zeldalink.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
nav{
  background-image: url(./components/triforcedribbble.png);
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 5px 5px 10px gold;
  background-position-y: -50px;
  display: flex; 
  justify-content: space-around;;
}
.god{
  height: 300px;
  overflow: hidden;
}
.wife{
  height: 300px;
  position: absolute;
  right: 200px;
  overflow: hidden;
}

  

li a{
  color: red !important;
}

.container{
  border: solid black;
  margin-top:30px;
  background-color: aliceblue;
  box-shadow: 5px 5px 10px gold;
  padding-left: 10px;
  padding-right: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

